.recipe{
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgb(71, 71, 71);
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background-color: white;
    align-items: center;
    padding: 10px;
    min-width: 40%;
}
.recipe a:hover{
    color: black;
}
.recipe a{
    color: blue;
    text-decoration: none;
    font-size: 16px;
    font-weight: 400;
}

.recipe h1{
    font-family: 'Anton', sans-serif;
    font-weight: lighter;
}
.recipe img{
    border-radius: 50%;
    height: 150px;
    width: 150px;
}
