.App{
  min-height: 100vh;
  /* background-image: linear-gradient(to top, #a8edea 0%, #fed6e3 100%); */
  background-image: linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%);
}

.search-form{
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar{
  width: 50%;
  border: none;
  padding: 10px;
}
.search-button{
  background-color: lightcoral;
  border: none;
  padding: 10px 20px;
  color: white;

}
.recepes{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.footer{
  width: 100%;
  text-align: right;
  font-size: 16px;
  position: relative;
  bottom: 0;
  background-image: linear-gradient(to top, #fad0c4 0%, #ffd1ff 100%);
  min-height: 40px;
  line-height: 2em;
  /* margin-top: 40px; */
}


